::placeholder {
  color: #999;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}

body,
html {
  @extend %box;
  background: #fff;
  //  font-size: $bodyFontSize;
  min-width: 1190px;
}

.right {
  float: right;
}

a.btn {
  color: #fff;
  text-decoration: none;
}

.icheckbox-warp {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  width: 16px;
  height: 16px;
  background: url("../../img/checkbox.png");
  border: none;
  background-position: 0 0;
  cursor: pointer;
  &.checked {
    background-position: -16px 0;
  }
}

.input {
  width: 100%;
  padding: 5px 20px;
  height: 40px;
  line-height: 30px;
  border-radius: 3px;
  border: 1px solid #d4d4d4;
}

.textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  line-height: 1.4;
  border: 1px solid $borderC;
  border-radius: 3px;
  resize: none;
}

@font-face {
  font-family: "iconfont";
  src: url('../../fonts/iconfont.eot'); /* IE9*/
  src: url('../../fonts/iconfont.eot#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../../fonts/iconfont.woff') format('woff'), /* chrome, firefox */
  url('../../fonts/iconfont.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
  url('../../fonts/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.icon:after {
  font-family: "iconfont";
  font-size: 16px;
  font-style: normal;
  line-height: 1;
}

.info-number .badge {
  font-size: 11px;
  font-weight: normal;
  line-height: 13px;
  padding: 2px 6px;
  position: absolute;
  left: 16px;
  top: 2px;
}

$bg: "info" "success" "warning" "primary" "danger" "dark";
$bgC: #6bd3f3 #53d192 #ffd200 #9c78cd #e55957 #2b2b2c;

@for $i from 1 through 6 {
  .bg-#{nth($bg,$i)},
  .info-number .bg-#{nth($bg,$i)} {
    color: #fff;
    background-color: nth($bgC, $i);
  }
}

@import "autocomplete";