@charset "UTF-8";
body,
html {
  width: 100%;
  height: 100%;
  position: relative; }

::placeholder {
  color: #999; }

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #F5F5F5; }

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5; }

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555; }

body,
html {
  background: #fff;
  min-width: 1190px; }

.right {
  float: right; }

a.btn {
  color: #fff;
  text-decoration: none; }

.icheckbox-warp {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  width: 16px;
  height: 16px;
  background: url("../../img/checkbox.png");
  border: none;
  background-position: 0 0;
  cursor: pointer; }
  .icheckbox-warp.checked {
    background-position: -16px 0; }

.input {
  width: 100%;
  padding: 5px 20px;
  height: 40px;
  line-height: 30px;
  border-radius: 3px;
  border: 1px solid #d4d4d4; }

.textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  line-height: 1.4;
  border: 1px solid #e7e7e7;
  border-radius: 3px;
  resize: none; }

@font-face {
  font-family: "iconfont";
  src: url("../../fonts/iconfont.eot");
  /* IE9*/
  src: url("../../fonts/iconfont.eot#iefix") format("embedded-opentype"), url("../../fonts/iconfont.woff") format("woff"), url("../../fonts/iconfont.ttf") format("truetype"), url("../../fonts/iconfont.svg#iconfont") format("svg");
  /* iOS 4.1- */ }

.icon:after {
  font-family: "iconfont";
  font-size: 16px;
  font-style: normal;
  line-height: 1; }

.info-number .badge {
  font-size: 11px;
  font-weight: normal;
  line-height: 13px;
  padding: 2px 6px;
  position: absolute;
  left: 16px;
  top: 2px; }

.bg-info,
.info-number .bg-info {
  color: #fff;
  background-color: #6bd3f3; }

.bg-success,
.info-number .bg-success {
  color: #fff;
  background-color: #53d192; }

.bg-warning,
.info-number .bg-warning {
  color: #fff;
  background-color: #ffd200; }

.bg-primary,
.info-number .bg-primary {
  color: #fff;
  background-color: #9c78cd; }

.bg-danger,
.info-number .bg-danger {
  color: #fff;
  background-color: #e55957; }

.bg-dark,
.info-number .bg-dark {
  color: #fff;
  background-color: #2b2b2c; }

.autocomplete-suggestions {
  padding: 5px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #999;
  cursor: default;
  background: #fff;
  overflow: auto;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.15); }

.autocomplete-suggestion {
  padding: 5px 18px;
  line-height: 30px;
  color: #383838;
  white-space: nowrap;
  overflow: hidden; }
  .autocomplete-suggestion:hover {
    background: #f0f0f0; }

.autocomplete-no-suggestion {
  padding: 2px 5px; }

.autocomplete-selected {
  background: #F0F0F0; }

.autocomplete-suggestions strong {
  color: #50d2c2; }

.login-warp {
  position: relative;
  background: url("../img/login-bg.jpg");
  background-size: cover; }

.signIn-warp {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0); }

.s-logo {
  height: 37px;
  margin-bottom: 24px;
  background-image: url("../img/s-logo.png");
  background-repeat: no-repeat; }

.s-main {
  width: 780px;
  padding: 25px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 8px; }

.input-box {
  display: inline-block;
  margin-left: 20px; }
  .input-box:first-child {
    margin-left: 0; }

.l-phone {
  width: 320px; }

.l-pwd {
  width: 190px; }

.l-submit {
  width: 170px;
  height: 40px; }

.l-bottom {
  margin-top: 21px;
  *zoom: 1;
  color: #fff; }
  .l-bottom:after, .l-bottom:before {
    content: "";
    display: table;
    font: 0/0 a; }
  .l-bottom:after {
    clear: both; }

a.a-btn {
  display: inline-block;
  margin: 0 7px;
  text-decoration: underline;
  color: #fff; }

.l-download {
  display: inline-block;
  margin-left: 15px; }
  .l-download label {
    display: inline-block;
    margin-right: 10px; }

.code-warp {
  position: absolute;
  display: none;
  top: 35px;
  left: 50%;
  padding: 10px;
  transform: translate3d(-50%, 0, 0);
  background: #fff; }
  .code-warp:after {
    content: "";
    display: inline-block;
    position: absolute;
    top: -8px;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    content: "";
    height: 0;
    width: 0;
    border-bottom: 8px solid #fff;
    border-left: 8px dashed transparent;
    border-right: 8px dashed transparent; }
  .icon:hover .code-warp {
    display: block; }

.code {
  display: inline-block;
  background-repeat: no-repeat;
  width: 133px;
  height: 133px;
  background-image: url("../img/down-code.jpg"); }

.icon {
  position: relative;
  display: inline-block;
  margin: 0 10px;
  cursor: pointer; }
  .icon i:after {
    font-family: iconfont;
    font-style: normal;
    font-size: 25px;
    color: #fff;
    line-height: 1; }
  .icon i.android:after {
    content: "\e655"; }
  .icon i.ios:after {
    content: "\e656"; }

/*弹层*/
.layer-bg {
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.layer-bg {
  background: rgba(0, 0, 0, 0.3); }

.layer-bg {
  position: absolute;
  z-index: 0; }

.modal.fade.layer-detail-warp .modal-dialog {
  transform: translate3d(100%, 0, 0);
  transition: transform .3s ease-out; }

.modal.in.layer-detail-warp .modal-dialog {
  transform: translate3d(0, 0, 0); }

.layer-detail-warp .modal-dialog {
  position: absolute;
  top: 60px;
  right: 0;
  bottom: 0;
  min-width: 800px;
  width: calc(100% - 280px);
  margin: 0;
  padding: 0; }

.layer-detail {
  background: #ebebeb;
  height: 100%; }

.layer-head {
  position: relative;
  padding: 10px 16px 10px 34px;
  line-height: 40px;
  background: #252834;
  font-size: 16px;
  color: #fff; }

.signUp-warp {
  padding: 30px;
  background: #f5f5f5; }
  .signUp-warp:before {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    bottom: 2px;
    right: 2px;
    border: 2px solid #252834; }
  .active .signUp-warp {
    animation-name: layerDown;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out; }

.signUp-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  overflow: hidden;
  cursor: pointer; }
  .signUp-close:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 200%;
    height: 100%;
    transform: rotate(45deg) translate3d(-26px, -8px, 0);
    background: #252834; }
  .signUp-close i {
    color: #fff;
    position: absolute;
    font-size: 35px;
    top: -7px;
    left: 30px; }

.signUp-main {
  padding: 10px 30px 20px; }

.s-title {
  margin-bottom: 30px;
  text-align: center;
  font-size: 22px;
  font-weight: bold; }

.s-box {
  width: 360px;
  margin: 0 auto; }

.s-input-box {
  position: relative;
  margin-bottom: 20px; }

.yzm-input {
